.container {
  width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.box {
  gap: 30px;
  display: flex;
}

.error {
  color: #ff3d00;
}

.box-img, .box-content {
  width: 50%;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.loader {
  width: 48px;
  height: 48px;
  box-sizing: border-box;
  border: 5px solid #fff;
  border-bottom-color: #ff3d00;
  border-radius: 50%;
  animation: rotation 1s linear infinite;
  display: inline-block;
}

@keyframes rotation {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.wrapper {
  width: 100vw;
  height: 100vh;
  z-index: 100;
  background-color: rgba(0, 0, 0, .6);
  justify-content: center;
  align-items: center;
  transition: opacity .5s;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.wrapper.is-hidden {
  opacity: 0;
  pointer-events: none;
}

.error.is-hidden {
  display: none;
}

